import React, {
  useEffect, useState, useRef, useMemo
} from 'react';
import Autocomplete from 'react-google-autocomplete';
import { shallowEqual } from 'react-redux';
import { parsePhoneNumber } from 'react-phone-number-input';
import { Input } from '@chakra-ui/react';
import { useAppSelector } from '../hooks/store';
import { formatGoogleMapData } from '../utils/gmap';

interface Address {
    addressType?: string;
    line1: string;
    line2: string;
    city: string;
    state: string;
    postalCode: string;
    country: string;
}

interface Props {
    googleApiKey: string;
    address?: Address;
    placeholder?: string;
    restrictedCountry?: string;
    international?: boolean;
    onChange: (d: Address) => void;
}

const AddressSearch = ({
  address, onChange, googleApiKey, placeholder, restrictedCountry, international
}: Props) => {
  // const googleApiKey = process.env.REACT_APP_GMAPS_KEY;
  const [addressState, updateAddress] = useState({ ...address });
  const [keyword, updateKeyword] = useState(address?.line1 || '');
  const addressRef = useRef<any>(null);

  const person = useAppSelector(
    (state) => state.user.personDetails,
    shallowEqual,
  );

  const userCountry = useMemo(() => {
    if (restrictedCountry) {
      return restrictedCountry;
    }
    if (person?.phone) {
      try {
        const phone = parsePhoneNumber(person?.phone);
        return phone?.country || 'US';
      } catch (e) {
        return 'US';
      }
    }
    return 'US';
  }, [person, restrictedCountry]);

  useEffect(() => {
    (window as any).gm_authFailure = () => {
      // GMAP error handling
      addressRef.current.disabled = false;
      addressRef.current.placeholder = '';
      addressRef.current.style.backgroundImage = '';
      addressRef.current.focus();

      return false;
    };
  }, []);

  useEffect(() => {
    if (address) {
      updateAddress(address);
      updateKeyword(address.line1);
    }
  }, [address]);

  const forGoogleMapData = (details: any) => {
    if (details) {
      const {
        line1, line2, city, state, postalCode, country
      } = formatGoogleMapData(details);
      const addressData = {
        ...addressState,
        line1,
        line2,
        city,
        state,
        country,
        postalCode,
      };
      updateAddress(addressData);
      onChange(addressData);
      updateKeyword(line1);
    }
  };

  return (

    <Input
      variant="primary"
      data-testid="address-line1"
      as={Autocomplete}
      ref={addressRef}
      apiKey={googleApiKey}
      onPlaceSelected={forGoogleMapData}
      value={keyword}
      placeholder={placeholder || ''}
      onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
        updateKeyword(e.target.value);
        const addressData: any = {
          ...addressState,
          // country,
          line1: e.target.value,
        };
        if (!international) {
          addressData.country = userCountry;
        }
        onChange(addressData);
      }}
      options={{
        types: ['address'],
        componentRestrictions: international ? undefined : { country: userCountry }
      }}
    />

  );
};

export default AddressSearch;
