import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, Flex, FormControl, FormLabel, Input, Switch, Text, useStyleConfig
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import AddressSearch from '../../../../components/AddressSearch';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';
import { apiErrorHandler } from '../../../../utils/general';
import { cardPayload } from '../../../../types/card';
import { updateCardPayload } from '../../../../store/actionCreators/card';
import { cardDelete, createCard } from '../../../../store/middlewares/card';
import { useDrawer } from '../../../../hooks/drawer';

const CardBillingAddress = React.lazy(() => import('../CardBillingAddress/CardBillingAddress'));
const CardCreated = React.lazy(() => import('../CardCreated/CardCreated'));

const CardAddress = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open, goBack } = useDrawer();

  const style:any = useStyleConfig('FormControl', {
    variant: 'primary'
  });
  const [isAddressSame, setAddressSame] = useState(true);
  const cardPayloadData = useAppSelector((store) => store.card.cardPayload, shallowEqual);
  const replaceCardId = useAppSelector((store) => store.card.replaceCardId);

  const setPayload = useCallback((key: keyof cardPayload, value: any) => {
    if (cardPayloadData) {
      const copiedData:cardPayload = {
        ...cardPayloadData,
        [key]: value
      };

      dispatch(updateCardPayload(copiedData));
    }
  }, [cardPayloadData]);

  const isValid = useMemo(() => (
    cardPayloadData
      && !!cardPayloadData.billingAddress
      && !!cardPayloadData.billingAddress.line1
      && !!cardPayloadData.billingAddress.city
      && !!cardPayloadData.billingAddress.state
      && !!cardPayloadData.billingAddress.country
      && !!cardPayloadData.billingAddress.postalCode
      && cardPayloadData.billingAddress.line1.length !== 0
      && cardPayloadData.billingAddress.city.length !== 0
      && cardPayloadData.billingAddress.state.length !== 0
      && cardPayloadData.billingAddress.country.length !== 0
      && cardPayloadData.billingAddress.postalCode.length !== 0
  ), [cardPayloadData]);

  const submit = useCallback(() => {
    if (isValid && cardPayloadData) {
      if (!isAddressSame) {
        open(CardBillingAddress);
      } else {
        cardPayloadData.shipping = {
          shippingAddress: cardPayloadData.billingAddress
        };
        dispatch(createCard(cardPayloadData))
          .then(() => {
            if (replaceCardId) {
              dispatch(cardDelete(replaceCardId))
                .catch(apiErrorHandler)
                .finally(() => {
                  open(CardCreated);
                });
            } else {
              open(CardCreated);
            }
          }).catch(apiErrorHandler);
      }
    }
  }, [isValid, replaceCardId, isAddressSame, cardPayloadData]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          heading={t(`create_card_info.title_${cardPayloadData?.cardType}`)}
          onBack={goBack}
          footer={(
            <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('create_card_address.ctaText')}
            </Button>
)}
        >
          <Box width="100%">
            {cardPayloadData?.cardType === 'physical'
              ? (
                <Flex alignItems="center" justifyContent="space-between">
                  <Box>
                    <Text variant="large" textAlign="left">
                      {t('create_card_address.confirmBillingAddressTitle')}
                    </Text>
                    <Text variant="regular" className="text-light" textAlign="left">{t('create_card_address.confirmBillingAddressDesc')}</Text>
                  </Box>
                  <Switch
                    variant="primary"
                    defaultChecked={isAddressSame}
                    onChange={() => {
                      setAddressSame(!isAddressSame);
                    }}
                  />
                </Flex>
              ) : null }
            <Text variant="large" mt={3}>
              {t('create_card_address.confirmMailingAddress')}
            </Text>
            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.address1')}
              </FormLabel>
              <AddressSearch
                placeholder={t('create_card_address.address1')}
                googleApiKey={process.env.REACT_APP_GMAP_KEY || ''}
                address={cardPayloadData?.billingAddress || undefined}
                restrictedCountry={cardPayloadData?.cardType === 'physical' ? 'US' : undefined}
                onChange={(address) => {
                  setPayload('billingAddress', {
                    ...address,
                    addressType: 'mailing',
                  });
                }}
              />
              {/* <Input variant="primary" value="" /> */}
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.address2')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.address2')}
                value={cardPayloadData?.billingAddress?.line2}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('billingAddress', {
                    ...cardPayloadData?.billingAddress || {},
                    line2: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressCity')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressCity')}
                value={cardPayloadData?.billingAddress?.city}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('billingAddress', {
                    ...cardPayloadData?.billingAddress || {},
                    city: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressState')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressState')}
                value={cardPayloadData?.billingAddress?.state}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('billingAddress', {
                    ...cardPayloadData?.billingAddress || {},
                    state: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary" style={style}>
              <FormLabel variant="primary">
                {t('create_card_address.addressZipcode')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('create_card_address.addressZipcode')}
                value={cardPayloadData?.billingAddress?.postalCode}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  setPayload('billingAddress', {
                    ...cardPayloadData?.billingAddress || {},
                    postalCode: e.target.value
                  });
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default CardAddress;
